.main {
  top: 80px;
  position: absolute;
}

.main h1 {
  padding: 20px 20px;
  color: white;
}

.main div {
  width: 100%;
}

.list {
  margin: 30px;
  padding: 0;
  --cols: auto-fit;

  display: grid;
  grid-template-columns: repeat(var(--cols), minmax(200px, 1fr));
  gap: 20px;
}

.list > li {
  margin: 0;
  padding: 0;
  list-style: none;
}