
span {
  /* font-weight: bold; */
  font-size: larger;
}

.title {
  font-weight: bold;
  text-align: right;
}

.cookie {
  background-color: #202020;
  display: block;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  padding-bottom: 20px;
}

.image {
  position: relative;
  top: 0px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  display: block;
  font-size: 8em;
  line-height: 1;
  padding-bottom: 10px;
}

.cookie ul {
  margin: 20px 0;
}

.contains {
  padding-inline-start: 0.5rem;
}