.form {
  margin-top: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
  
.form label {
  display: flex;
  margin: 10px 0;
}
  
.form label span {
  font-size: 20px;
}
  
.form label input {
  width: 300px;
  height: 35px;
  padding: 5px;
  background-color: #202020;
  color: #fff;
  border: solid 2px gray;
  border-radius: 5px;
  font-size: 20px;
}

.form span {
  min-width: 70px;
}

.form button {
  cursor: pointer;
  margin-top: 10px;
  width: 100px;
  height: 40px;
  color: white;
  background-color: #404040;
  font-size: 17px;
}
